import { useState, useEffect } from "react";

// Use React Hooks for incrementing, decrementing nd resetting the value of the counter.
// Also, print the value of count on the console by using useEffect hook.

function Counter() {
  const count = 0;
  const [value, setValue] = useState(count);
  useEffect(() => {
    document.title = `you manupulate the calue`;
  });
  return (
    <>
      <p style={styles.style}>Count value is: {value}</p>
      <button style={styles.button} onClick={() => setValue(count)}>
        Reset
      </button>
      <button style={styles.button} onClick={() => setValue(value + 1)}>
        Plus (+)
      </button>
      <button style={styles.button} onClick={() => setValue(value - 1)}>
        Minus (-)
      </button>
    </>
  );
}

export default Counter;
const styles = {
  style: {
    color: "teal",
    fontSize: "2rem",
    fontWeight: "900"
  },
  button: {
    color: "red",
    padding: "10px",
    margin: "5px",
    fontSize: "1rem",
    fontWeight: "900",
    border: "2px solid teal",
    cursor: "pointer"
  }
};
